import React, {useEffect, useRef} from 'react';
import Contacts from "../Contacts/Contacts";
import {projects} from "../../data/projects.js";
import "./Stack.scss"

import {gsap} from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);


const StackList = () => {
    const scroller = useRef();
    const stackItems = useRef();

    useEffect(() => {
        let stackItems = gsap.utils.toArray(".stack__item");
        gsap.set(stackItems, { autoAlpha: 0});
        gsap.to(stackItems, {duration: 1, x: 400, force3D: true, autoAlpha: 1});

        let to = gsap.to(stackItems, {
            xPercent: () => -100 * ((stackItems.length * 2)),
            ease: "none",
            duration: 50,

            scrollTrigger: {
                trigger: scroller.current,
                // markers: true,
                pin: true,
                pinSpacing: true,
                scrub: 0.5,
                invalidateOnRefresh: true,
                anticipatePin: 1,
                // snap: 1 / (stackItems.length - 1),
                end: () => "+=" + window.innerWidth,

            },
        });

        return () => {
            to.kill();
        };
    }, []);

    return (
        <div ref={scroller}>
            <div  className="stack__container">
                {projects.map((slideContent, index) => (
                    <div className="stack__item"  key={slideContent.id} ref={stackItems}>
                        <a href={slideContent.preview} className="stack__link">
                            <div className="stack__img">
                                <img src={slideContent.background} alt="" className=""/>
                                <div className="stack__desc">
                                    <div className="stack__desc-wrap">
                                        <p className="stack-list__description text-2xl text-center">         {slideContent.description}     </p>
                                        <ul className="stack-labels flex flex-wrap gap-3 justify-center m-3">         {slideContent.labels.map((labels, id) =>
                                            <li key={id} className="stack-labels__item p-1">{labels}</li>)}     </ul>
                                    </div>
                                </div>
                            </div>
                        </a>
                    </div>
                ))}
            </div>
        </div>
    )
};

export default StackList;
